import { defaultValues } from "./value-tree";
export async function updateCalculations(key, value, display) {
  await updateState.call(this, key, value);
  if (display) {
    let displayKey = `${key}Pct`;
    await updateState.call(this, displayKey, display);
  }
  await setAverageUserFullyLoaded.call(this);
  await setSavings.call(this);
  await setCost.call(this);
  await calculateROI.call(this);
}

export async function initDefaultValues() {
  this.setState(
    {
      numberOfUsers: defaultValues.numberOfUsers,
      averageAnnualSalary: defaultValues.averageAnnualSalary,
      fullyLoadedCostMultiplier: defaultValues.fullyLoadedCostMultiplier,
      outreachVoice: defaultValues.outreachVoice,
      outreachVoicPct: -1,
      permissionsAccess: defaultValues.permissionsAccess,
      permissionsAccessPct: -1,
      protectReputation: defaultValues.protectReputation,
      protectReputationPct: -1,
      perUserCost: defaultValues.perUserCost,
      hasInitialized: true,
      hoursSaved: defaultValues.hoursSaved,
      daysSaved: defaultValues.daysSaved,
      annualSavings: defaultValues.annualSavings,
      calcCost: 0,
      calcProductivity: 0,
      calcNet: 0,
      calcROI: 0
    },
    async () => {
      await setAverageUserFullyLoaded.call(this);
      await setSavings.call(this);
    }
  );
}

function updateState(key, value) {
  return new Promise(resolve => {
    this.setState(
      {
        [key]: value
      },
      () => {
        resolve();
      }
    );
  });
}

function setAverageUserFullyLoaded() {
  return new Promise(resolve => {
    const averageUserFullyLoadedCost =
      toNumber(this.state.averageAnnualSalary) *
      toNumber(this.state.fullyLoadedCostMultiplier);
    const averageUserFullyLoadedCostHourly = averageUserFullyLoadedCost / 2080;
    this.setState(
      {
        averageUserFullyLoadedCost,
        averageUserFullyLoadedCostHourly
      },
      () => resolve()
    );
  });
}

function setSavings() {
  const OUTREACH_FACTOR = 5;
  const PERMISSIONS_FACTOR = 2;
  const REP_FACTOR = 1;
  const outreachPct = toNumber(this.state.outreachVoicePct) || 0;
  const permissionsAccessPct = toNumber(this.state.permissionsAccessPct) || 0;
  const protectReputationPct = toNumber(this.state.protectReputationPct) || 0;

  return new Promise(resolve => {
    if (
      outreachPct < 0 ||
      permissionsAccessPct < 0 ||
      protectReputationPct < 0
    ) {
      resolve();
    } else {
      let outreachNum = outreachPct * 0.01 * OUTREACH_FACTOR;
      let permissionNum = permissionsAccessPct * 0.01 * PERMISSIONS_FACTOR;
      let repNum = protectReputationPct * 0.01 * REP_FACTOR;
      const hoursSaved = outreachNum + permissionNum + repNum;
      const daysSaved = hoursSaved / 8;
      const annualSavings =
        this.state.numberOfUsers *
        this.state.averageUserFullyLoadedCostHourly *
        hoursSaved *
        12;
      this.setState(
        {
          hoursSaved,
          daysSaved,
          annualSavings
        },
        () => resolve()
      );
    }
  });
}

function setCost() {
  return new Promise(resolve => {
    this.setState(
      {
        contractCost: this.state.numberOfUsers * this.state.perUserCost * 12
      },
      () => resolve()
    );
  });
}

function toNumber(value) {
  if (!value) {
    return;
  }
  if (isNaN(value)) {
    let regEx = new RegExp("[^0-9|.]", "gi");
    let formattedNumber = value.replace(regEx, "");
    return formattedNumber;
  } else return value;
}

const currency = value => {
  value = value + "";
  const reg = new RegExp("[^0-9|.]", "gi");
  let number = value.replace(reg, "");
  return Number(number).toLocaleString("en-US", {
    style: "currency",
    currency: "USD"
  });
};

function calculateROI() {
  return new Promise(resolve => {
    const calcNet = (
      this.state.annualSavings - this.state.contractCost
    ).toFixed(2);
    const calcROI =
      ((calcNet / this.state.contractCost) * 100).toFixed(2) + "%";
    this.setState(
      {
        calcCost: currency(this.state.contractCost),
        calcProductivity: currency(this.state.annualSavings),
        calcNet: currency(calcNet),
        calcROI
      },
      () => resolve()
    );
  });
}
